<template>
	<view class=""  style="height: 100vh; overflow: hidden; position: relative;">
	<view v-if="ifLogin" class="">
		<view v-show="add || Flag" class="">
			<TekyGoTop></TekyGoTop> 
		</view>
		<popUp ref="miles"></popUp>
		<view class="" v-show="Flag">
			<view class="ta-c FFF f34rpx fw700" 
				style="padding: 50rpx 0 40rpx; background-color: #eb5d2d;">WHO'S PLAYING?</view>
			<scroll-view  scroll-y="true" class="" 
				style="background-color: #fdc84f; height: calc(100vh - 270rpx)">
				<view class="" style="height: 70rpx;"></view>
				<view v-for="(item,index) in myData" :key="item.id" 
					class="df aic" style=" margin: 0 0 50rpx 50rpx;">
					<view class="df b-r-50"  @click="personalData(item)"
						hover-class="hover" hover-start-time=0 hover-stay-time=150 
						style="width: 220rpx; height: 225rpx; background-color: #fff9d5; border: 8rpx solid #ea521f;">
						<view class="df-jcc-aic" style="">
							<image :src="arr[item.avatarId]" mode="widthFix" style="width: 210rpx;"></image>
						</view>
					</view>
					<view class="f38rpx fw800" style="width: 400rpx; padding-left: 50rpx; color: #eb5d2d; word-wrap: break-word;"
					>{{item.nickname}}</view>
				</view>
				<view  @click.stop="Add" class="df" style="align-items: center; margin-left: 50rpx;">
					<view class="b-r-50 df-jcc-aic" 
						hover-class="hover" hover-start-time=0 hover-stay-time=150 
						style="border: 8rpx solid #ea521f; width: 228rpx; height: 233rpx; background-color: #fff9d5;">
						<view class="" style="color: #ea521f; font-size: 80rpx;">+</view>
					</view>
					<view class="f38rpx fw800" style="padding-left: 50rpx; color: #eb5d2d;">Add</view>
				</view>
				<view  @click.stop="deletes" class="df" style="align-items: center; margin: 50rpx 0 50rpx 50rpx;">
					<view class="b-r-50 df-jcc-aic" 
						hover-class="hover" hover-start-time=0 hover-stay-time=150 
						style="border: 8rpx solid #ea521f; width: 228rpx; height: 233rpx; background-color: #fff9d5;">
						<view class="" style="color: #ea521f; font-size: 90rpx;">-</view>
					</view>
					<view class="f38rpx fw800" style="padding-left: 50rpx; color: #eb5d2d;">Delete</view>
				</view> 
				<view  class="" style="height: 200rpx;"></view>
			</scroll-view>
		</view>
		<view class="" v-show="add">
			<!-- 返回 -->
			<view class="" @click.stop="cutBack" 	
				hover-class="hover1" hover-start-time=0 hover-stay-time=150 
				style="position: absolute; top: 150rpx; left: 50rpx;">
				<image src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
			</view>
			<Add @cutBack="cutBack" @mOpenPopup="mOpenPopup" @getMyData="getMyData"></Add>
		</view>
		<view v-show="perDataFlag" class="">
			<TekyGoTop tekyFlag="tekyFlag" ></TekyGoTop> 
			<perData @personalBack="personalBack" 
			:currentValue="currentValue" 
			:MonthDatas="MonthDatas"
			:id="ids" ></perData> 
		</view> 
		<view class="bg" v-show="delet" style="width: 750rpx;">
			<popUp res="dele"></popUp> 
			<view class="" style="height: 205rpx;"></view>
			<view class="df" style="align-items: center;">
				<view @click.stop="deleteBack" class="" style="margin: 0 50rpx;"
					hover-class="hover1" hover-start-time=0 hover-stay-time=150 >
					<image src="../../static/Index/fanHui.png" mode="widthFix" style="width: 25rpx;"></image>
				</view>
				<view @click="deleteMyRole" class="ta-c dele fw800" style=""
					hover-class="hover" hover-start-time=0 hover-stay-time=150 >DELETE</view>
			</view>
			<view class="">
				<scroll-view scroll-y="true" style="height: calc(100vh - 285rpx);">
					<view class="" style="height: 70rpx;"></view>
					<checkbox-group @change="changeDelete($event,item.id)" v-for="(item,index) in myData" :key="item.id" 
						class="df-jcsb" style="align-items: center; margin: 0 0 50rpx 50rpx;">
						<view class="df" style="align-items: center;">
							<view class="bgc_FFF b-r-50" style="border: 8rpx solid #eb5d2d;">
								<image :src="arr[item.avatarId]" mode="widthFix" 
								style="width: 220rpx; border-radius: 50%;"></image>
							</view>
							<view class="f38rpx fw800" style="width: 250rpx; padding-left: 50rpx; color: #eb5d2d; word-wrap: break-word;">{{item.nickname}}</view>
						</view>
						
						<view  class="b-r-50 bgc_FFF df-jcc-aic"
							style="height: 100rpx; width: 100rpx; margin-right: 50rpx; overflow: hidden;">
							<label class=""  style="margin-left: 20rpx;">
								<checkbox :value="item.id" color="#eb5d2d" style="transform:scale(2.5);"  />
							</label>
						</view>
					</checkbox-group>
					<view class="" style="height: 70rpx;"></view>
				</scroll-view>
			</view>
		</view>
	</view>  
	
	<view class="showmodel" v-show="ifGo">
		<view class="" style="height: 30vh;"></view>
		<view class="bgc_FFF" 
		style="margin: 0 78rpx; border-radius: 51rpx; height: 500rpx; border: 1rpx solid #9ec0d7;">
			<view class="df-jcc-aic" style="">
				<image  src="../../static/fristPage/Head3.png" mode="widthFix" 
				style="width: 166rpx; margin-top: 30rpx;"></image>
			</view>  
			<view class="ta-c color-H0f649c f33rpx" style="margin: 20rpx 20rpx 20rpx; ">
				{{content}}
			</view>
			<view class="FFF f33rpx ta-c bgc-o-r fw800" @click="goLogin"
			style="line-height: 84rpx; margin: 0 42rpx; height: 84rpx; border-radius: 42rpx; ">
				{{key}} 
			</view>
		</view>
	</view>
	</view>
</template>

<script>
	import Bus from '../../util/eventBus.js'
	import Add from './Add.vue'
	// import addus from './addus.vue'
	import perData from './personalData.vue'
	var view = null
	export default {
		components:{Add,perData,},
		data() {
			return {	 
				Flag:true,
				add:false,
				perDataFlag:false,
				delet:false,
				ifLogin:true,
				ifGo:false,
				myData:'',
				content:'We see that you are not subscribed! Please subscribe to access this feature and many more!',
				key:'Login',
				arr:["../../static/mlestones/left1.png","../../static/mlestones/left2.png","../../static/mlestones/left3.png","../../static/mlestones/right4.png","../../static/mlestones/right5.png","../../static/mlestones/right6.png"],
				currentValue:{},
				ids:'',
				isChange:[], //选中要删除的ID
				isChange1:{}, //选中要删除的ID
				MonthDatas:{},
				Data:20,
				flags:1


			}
		},
		onLoad() {
			uni.showLoading({
				title:'Loading...'
			})
			let time2 = setTimeout(function() {
				uni.hideLoading()
				console.log('1');
				// clearTimeout(time)   
			}, 1800); 
		},
		methods:{

			//获取当月游戏数据
			async getMonthData(id){
				let year = new Date().getFullYear()
				let month = new Date().getMonth()+1
				if(month<10){
					month = '0' + month
				}
				// console.log(month);
				const res1 = await this.$myRequest({
					url:'/api/game/data/overview' + '?playerId=' + id + '&time='+ year +'-' + month +'-01'
				})
				console.log('res1');
				console.log(res1.data.data.datas[0]);
				this.MonthDatas = res1.data.data.datas[0]
			},
			
			goLogin(){
				uni.reLaunch({
					url:'../welcomePage/inputEmail'
				})
			},
			tankaung(){
				console.log('tank');
				this.$refs.miles.openPopup('Successfully added')
			},
			Add(){
				this.Flag = false
				this.perDataFlag = false
				this.add = true
			},
			cutBack(){
				this.Flag = true
				this.perDataFlag = false
				this.add = false
			}, 
			mOpenPopup(){
				this.$refs.miles.openPopup('Successfully added')
			},
			personalData(e){
				// this.getMonthData(e.id)
				// Bus.$emit('MonthData',this.MonthData)
				this.currentValue = e
				console.log(this.currentValue);
				// console.log(e.id);
				this.ids = e.id
				// this.getMonthData(e.id)
				// this.getDayData(e.id)
				this.Flag = false
				this.add = false
				this.perDataFlag = true
			},
			personalBack(){
				this.Flag = true
				this.add = false
				this.perDataFlag = false
			},
			deleteBack(){
				this.delet = false
			},
			deletes(){
				this.delet = true
			},

			changeDelete(e,id){
				var _this = this
				let ids = e.detail.value[0]
				if(typeof(ids) != 'undefined'){
					this.isChange.push(id);//选中添加到数组里
				}else{
					this.isChange.splice(id,1) //从数组中删除
				} 
				// console.log(this.isChange); 
			}, 
			
			// 删除玩家信息
			async deleteMyRole(){
				var _this = this
				if(_this.isChange.length == 0){
					// console.log('空');
					return false
				}
				let JSON_arr = this.isChange
				let json = []
				for(var i= 0; i<JSON_arr.length; i++){
					var j = {}
					j.id = JSON_arr[i]
					json.push(j)
				}
				let idData = JSON.stringify(json)
				// console.log(idData); 
				const res = await this.$myRequest({
					url:'/api/player/delete',
					method:'POST', 
					data:idData 
				})
				// console.log(res);
				if(res.data.succeeded){
					_this.getMyData()
					_this.delet = false
					_this.isChange = []
					_this.$refs.miles.openPopup('Successfully delete')
					
				}else{
					_this.$refs.miles.openPopup('An error occurred. Please try again')
				}
			},
			
			//获取玩家列表
			async getMyData(){
				var _this = this
				const res = await this.$myRequest({
					url:'/api/player/list' + '?page=1' + '&limit=-1'
				})
				console.log(res);
				_this.myData = res.data.data.records
				// console.log(_this.myData);
			},
			// 刷新token
			async getToken(){
				let email = uni.getStorageSync('Email')
				let possword = uni.getStorageSync('Password')
				if(email != '' && possword != ''){
					const {data:res} = await this.$myRequest({
						method:'POST',
						url:'/api/auth/login',
						data:{
							"email":email,
							"password":possword
						}
					}) 
					console.log(res)
					console.log('刷新token')
					if(res.data){
						uni.setStorageSync('token',res.data.token)
					}
				}else{
					return false
				}
			}
		}, 
		created() {
			this.getMyData() 
			let ss = uni.getStorageInfoSync()
			console.log('缓存信息');
			console.log(ss);
		},
		onShow() {
			this.getMyData() 
			
			let _this = this
			uni.getNetworkType({
					success: function (res) {
							console.log(res.networkType);
							if(res.networkType == 'none'){
								// console.log('无网络');
								_this.flags = 2
							}else{
								// console.log('有网络');
								_this.flags = 1
							}
					}
			});
			this.$nextTick(function(){
				if(this.flags == 2){
					this.$refs.miles.openPopup('Request out of time, please check network and try it again')
				}else{
					
				}
			})
		},
		updated() {
			// this.getMyData()  
		},
		onTabItemTap(){
			let isLogin = uni.getStorageSync('isLogin')
			if(!isLogin){
				this.ifGo = true
			}else{
				this.ifGo = false
				this.getToken()
			} 
		}
	}
</script>

<style scoped> 
	page{background-color: #fdc84f;}
	.bg{
		background-color: rgba(0, 0, 0, 0.5);
		z-index: 20;
		height: 100 vh;
		position: absolute;
		top: 0;
		left: 0;
	}
	.dele{
		height: 80rpx; 
		background-color: #fdca50; 
		width: 500rpx;
		line-height: 80rpx;
		color: #d3520c;
		border-radius: 20rpx;
	}
	.showmodel{
		z-index: 20;
		background: rgba(36,36,36,0.9);
		position: fixed;
		left: 0;
		right: 0;
		bottom: -100rpx; 
		top: 0;
	}
</style>



	  



